.module-dialog-layer {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.8;
  filter: alpha(opacity=80);
}
.module-dialog {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1001;
  text-align: center;
  opacity: 0;
  transition: opacity 0.2s ease-in;
}
.module-dialog:after {
  content: '';
  height: 100%;
  margin-left: -0.25em;
}
.module-dialog .dialog-panel,
.module-dialog:after {
  display: inline-block;
  vertical-align: middle;
}
.module-dialog .dialog-panel {
  position: relative;
  width: 436px;
  min-width: 200px;
  background: #fff;
  border-radius: 8px;
  border: 1px solid #dcdcdc;
  border-color: rgba(0, 0, 0, 0.14);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 1);
  text-align: left;
}
.module-dialog .topbar {
  overflow: hidden;
  width: 100%;
  height: 69px;
  border-radius: 13px 13px 0 0;
}
.module-dialog .dialog-tit {
  height: 60px;
  padding: 0 15px;
  border-radius: 8px 8px 0 0;
  border-bottom: 1px solid #d4d4d4;
  box-shadow: rgba(0, 0, 0, 0.06) 0 1px 7px;
  background: linear-gradient(#fbfbfb, #ececec);
  line-height: 60px;
}
.module-dialog .dialog-tit h4 {
  float: left;
  padding-left: 15px;
  font-size: 18px;
  font-weight: 400;
  color: #333;
}
.module-dialog .dialog-close {
  overflow: hidden;
  display: block;
  position: absolute;
  right: 17px;
  top: 18px;
  z-index: 20;
  width: 16px;
  height: 17px;
  padding: 4px 5px;
  background: url(/static/media/ui.00b79aea.png) no-repeat -16px -16px;
  text-indent: -9999em;
  opacity: 0.7;
  cursor: pointer;
  transition: all 0.3s linear;
}
.module-dialog .dialog-close:hover {
  opacity: 1;
}
.module-dialog .dialog-con {
  min-width: 220px;
}
.module-dialog .confirm-error-msg,
.module-dialog .confirm-msg {
  max-width: 310px;
  min-height: 50px;
  margin: 50px auto;
  line-height: 24px;
  text-align: center;
  word-wrap: break-word;
  word-break: break-all;
  font-size: 16px;
}
.module-dialog .dialog-btn-wrap {
  height: 51px;
  padding: 18px 10px 0;
  line-height: 51px;
  border-radius: 0 0 13px 13px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAABFCAYAAACL3IzzAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAADpJREFUeNpi+P//PwMIMDEBCWYQwYJKsIIIXoYfP378ZwIrRBAgvehiRMiSJEGEGJnaqOhILLIAAQYAPiIuZT5QGrcAAAAASUVORK5CYII=) 0 0 repeat-x;
}
.blue-main-btn,
.gray-main-btn,
.jianguo-blue-main-btn {
  display: block;
  padding: 1px;
  margin: 0 auto;
  border-radius: 9px;
  background: #015e94;
  background: linear-gradient(#5598c9, #2a6da2);
  text-align: center;
  text-shadow: rgba(255, 255, 255, 0.496094) 0 1px 0;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.normal-main-btn {
  height: 38px;
}
.module-dialog .dialog-btn-wrap .blue-main-btn {
  float: right;
}
.module-dialog .dialog-btn-wrap .blue-main-btn,
.module-dialog .dialog-btn-wrap .blue-main-btn a {
  width: 100px;
}
.blue-main-btn a,
.gray-main-btn a,
.jianguo-blue-main-btn a {
  display: block;
  padding: 2px 0;
  border-radius: 9px;
  background: #5f7ed7;
  background: linear-gradient(#739fe1, #5f7ed7);
  box-shadow: inset 0 1px 3px #92b6ec, inset 0 0 2px #627dca,
    inset 0 -2px 3px #4c69b8;
  text-shadow: #4f70b3 0 -1px 0;
  cursor: pointer;
  color: #fff;
  transition: all 0.3s ease;
}
.normal-main-btn a {
  height: 34px;
  line-height: 34px;
}
.blue-main-btn a:hover {
  box-shadow: inset 0 1px 3px #83a9e1, inset 0 0 2px #627dca,
    inset 0 -2px 3px #5a77c7, inset 0 0 100px rgba(48, 77, 147, 0.4);
}
.blue-main-btn a:active {
  box-shadow: inset 0 2px 5px #3d67a5, inset 0 0 2px #627dca,
    inset 0 0 100px rgba(25, 108, 232, 0.5);
}
.sku-box{
  position: relative;
}
.sort-option{
  border-top: 1px solid #D8D8D8;
  color: #999;
}
.sort-option ul{
  height: 60px;
  line-height: 60px;
}
.sort-option li{
  position: relative;
  float: left;
  padding-left: 42px;
}
.sort-option li:first-child{
  padding-left: 9px;
}
.sort-option li:before{
  content: ' ';
  display: block;
  position: absolute;
  left: 20px;
  top: 50%;
  width: 2px;
  height: 2px;
  margin-top: -1px;
  background: #C7C7C7;
}
.sort-option li:first-child:before{
  display: none;
}
.sort-option a{
  display: block;
  font-size: 12px;
  color: #999;
}
.sort-option a.active, .sort-option a:hover{
  color: #5683EA;
}
.gray-box{
  overflow: hidden;
  background: #fff;
  border-radius: 8px;
  border: 1px solid #dcdcdc;
  border-color: rgba(0,0,0,.14);
  box-shadow: 0 3px 8px -6px rgba(0,0,0,.1);
}
.sku-box .item-box{
  clear: both;
  overflow: hidden;
  margin: 0 -1px -1px -1px;
}
.sku-box .item{
  position: relative;
  float: left;
  border-right: 1px solid #efefef;
  border-bottom: 1px solid #efefef;
  width: 25%;
  height: 429px;
  background: #fff;
  box-sizing: border-box
}
.sku-box .item:hover{
  box-shadow: 0 0 38px rgba(0,0,0,.08) inset;
  transition: all .15s ease;
}
.sku-box .item .item-img img{
  display: block;
  width: 206px;
  height: 206px;
  margin: 50px auto 10px;
}
.sku-box .item h3, .sku-box .item h6{
  overflow: hidden;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.sku-box .item h6{
  line-height: 1.2;
  font-size: 16px;
  color: #424242;
  margin: 0 auto;
  padding: 0 14px;
}
.sku-box .item h3{
  line-height: 1.2;
  font-size: 12px;
  color: #d0d0d0;
  margin: 8px auto 14px;
}
.sku-box .item .params-colors{
  margin-top: 23px;
  text-align: center;
}
.sku-box .item .colors-list{
  display: inline-block;
  overflow: hidden;
}
.sku-box .item .colors-list li{
  float: left;
  margin: 0 5px;
}
.sku-box .item .colors-list>li a{
  width: 8px;
  height: 8px;
  border: 1px solid #e5e5e5;
  border-radius: 50%;
  padding: 2px;
  display: block;
}
.sku-box .item .colors-list>li a.active{
  box-shadow: inset 0 0 0 1px #b2b2b2;
  border-color: #b2b2b2;
}
.sku-box .item .colors-list>li img{
  width: inherit;
  height: inherit;
  border-radius: 50%;
  display: block;
}
.sku-box .item .item-btns{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 29px;
  text-align: center;
  opacity: 0;
  z-index: 10;
}
.sku-box .item:hover .item-btns{
  opacity: 1;
  transition: all .2s ease-in;
}
.sku-box .item .item-btns .item-blue-btn, .sku-box .item .item-btns .item-disabled-btn, .sku-box .item .item-btns .item-gray-btn, .sku-box .item .item-btns .item-green-btn{
  display: inline-block;
  box-sizing: border-box;
  width: 100px;
  height: 30px;
  font-size: 12px;
  line-height: 28px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 200;
  transition: all .1s ease;
}
.sku-box .item .item-btns .item-gray-btn{
  border: 1px solid #d5d5d5;
  color: #646464;
}
.sku-box .item .item-btns .item-gray-btn a{
  display: block;
  color: #a1a1a1;
}
.sku-box .item .item-btns .item-gray-btn:hover{
  background-image: linear-gradient(#f6f6f6,#ededed);
}
.sku-box .item .item-btns .item-blue-btn{
  background-color: #5c85e5;
  background-image: linear-gradient(#779ae9,#5078df);
  border: 1px solid #5c81e3;
  color: #fff;
  margin-left: 10px;
}
.sku-box .item .item-btns .item-blue-btn:hover{
  border: 1px solid #5374c8;
  background-color: #5074db;
  background-image: linear-gradient(#6e8ed5,#4769c2);
}
.sku-box .item .item-btns .item-blue-btn:active{
  border: 1px solid #3e61d7;
  background-color: #5c85e5;
  background-image: linear-gradient(#4d72de,#6189e6);
}
.sku-box .item .item-price{
  font-family: Arial;
  color: #c30a18;
  margin-top: 24px;
  text-align: center;
  opacity: 1;
}
.sku-box .item .item-price i{
  font-size: 16px;
}
.sku-box .item .item-price span{
  font-size: 18px;
  padding-left: 4px;
}
.sku-box .item:hover .item-price{
  opacity: 0;
  transition: all .1s ease-out;
}
.sku-box .item .discount-icon{
  display: none;
}
.sku-box .item .item-cover a{
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 20;
  width: 100%;
  height: 310px;
}
